	module.exports = {"pathContext":{"docHtml":"<h1>decamelize <a href=\"https://travis-ci.org/sindresorhus/decamelize\"><img src=\"https://travis-ci.org/sindresorhus/decamelize.svg?branch=master\" alt=\"Build Status\"></a></h1>\n<blockquote>\n<p>Convert a camelized string into a lowercased one with a custom separator<br>\nExample: <code>unicornRainbow</code> → <code>unicorn_rainbow</code></p>\n</blockquote>\n<h2>Install</h2>\n<pre><code>$ npm install --save decamelize\n</code></pre>\n<h2>Usage</h2>\n<pre><code class=\"language-js\">const decamelize = require('decamelize');\n\ndecamelize('unicornRainbow');\n//=> 'unicorn_rainbow'\n\ndecamelize('unicornRainbow', '-');\n//=> 'unicorn-rainbow'\n</code></pre>\n<h2>API</h2>\n<h3>decamelize(input, [separator])</h3>\n<h4>input</h4>\n<p>Type: <code>string</code></p>\n<h4>separator</h4>\n<p>Type: <code>string</code><br>\nDefault: <code>_</code></p>\n<h2>Related</h2>\n<p>See <a href=\"https://github.com/sindresorhus/camelcase\"><code>camelcase</code></a> for the inverse.</p>\n<h2>License</h2>\n<p>MIT © <a href=\"https://sindresorhus.com\">Sindre Sorhus</a></p>","docPath":"argo-ci/node_modules/decamelize/readme","proj":"argo-ci"}}

